
import { defineComponent } from "vue";
import { WeatherData } from "@/model/WeatherData";
import { codes } from "@/model/WeatherCodes";

export default defineComponent({
  data() {
    return { weatherData: {} as WeatherData };
  },
  async mounted() {
    const weatherData = await (
      await fetch(
        "https://unruffled-liskov-633292.netlify.app/.netlify/functions/currentWeather",
        { method: "GET", headers: { "Content-Type": "application/json" } }
      )
    ).json();
    const data = weatherData.sensors[0].data[0];
    console.log(data);
    this.weatherData = WeatherData.fromRaw(data);
  },
  computed: {
    forecast() {
      return codes[(this.weatherData.forecastCode || 200).toString()];
    },
    icon() {
      const forecast = this.forecast;
      //TODO - Ovviamente non va bene
      if (forecast.includes("nuvoloso")) {
        return "wb_cloudy";
      }
      if (forecast.includes("soleggiato")) {
        return "wb_sunny";
      }
      if (forecast.includes("precipitazioni")) {
        return "flood";
      }
      if (forecast.includes("vento")) {
        return "wind_power";
      }
      if (this.weatherData.temp > 22 || forecast.includes("caldo")) {
        return "local_fire_department";
      }
      return "wb_sunny";
    },
  },

  methods: {
    //
  },
});
